#Ari{
  display: none;
}

#m {
  display: none;
}

.checkbtn {
  font-size: 30px;
  color: white;
  background: none;
  border: none;
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  cursor: pointer;
  display: none;
}

#check {
  display: none;
}
nav {
  background-color: black;
  color: white;
  padding: 0px 20px 1px 1%;
}
nav ul {
  font-size: large;
  list-style: none;
  background-color: transparent;
  margin-top: 0;
  margin-left: 51.3%;
  box-sizing: border-box;
  font-weight: bolder;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  display: inline;
}

nav li {
  padding: 1% 1%;;
  display: inline-block;
}

nav a {
  position: relative;
  text-decoration: none;
  display: inline-block;
  padding: 0.5em 1em;
  color: aliceblue;
  z-index: 1;
  background-color: black;
  border-radius: 10%;
  box-shadow: 10px 0px 20px rgb(241, 5, 5);
  border: 2px solid;
  border-image: linear-gradient(240deg, rgb(233, 141, 3), rgb(255, 0, 0)) 1;
}

nav a:hover {
  box-shadow: 10px 0px 20px rgb(11, 212, 234);
  border-image: linear-gradient(240deg, rgb(9, 228, 206), rgb(42, 18, 193)) 1;
}
nav a:focus {
  box-shadow: 10px 0px 20px rgb(11, 212, 234);
  border-image: linear-gradient(240deg, rgb(9, 228, 206), rgb(42, 18, 193)) 1;
}


