@media screen and (max-width: 1024px) {
  #m {
    display: block;
    background-color: rgb(125, 124, 124);
    border-radius: 0%;
    position: relative;
    color: white;
    font-weight: bold;
    padding: 10% 10%;
    margin-left: -85%;
    margin-top: -1vh;
    width: 100vw;
    /* display: inline; */
    
  }
  #check:checked ~ ul {
    margin-left: -1%;
    padding: 0% 30%;
    background: linear-gradient(220deg, rgb(255, 255, 255), rgb(255, 255, 255));
    position: fixed;
    background-size: 30px;
    width: 100%;
    display: block;
    z-index: 99999;
    box-sizing: border-box;
    
  }

  #check:checked ~ .checkbtn .fa-bars {
    display: none;
  }
  .checkbtn .fa-times {
    display: none;
  }

  .checkbtn .fa-bars {
    display: block;
  }
  #check:checked ~ .checkbtn .fa-times {
    display: block;
  }

  .checkbtn {
    display: block;
    position: fixed;
    margin-left: 90%;
    background: linear-gradient(
      45deg,
      black,
      rgb(254, 254, 254),
      rgb(5, 245, 137),
      rgb(0, 0, 0),
      black
    );

    -webkit-background-clip: text;
    color: transparent;

    padding: 3%;
  }
  #check:not(:checked) ~ nav ul {
    display: none;
  }
  nav ul {
    margin-left: 30%;
    margin-top: 50%;
    padding: 0% 8% 8%;

    position: fixed;
    background-size: 30px 30px;
    display: none;
  }

  nav li {
    display: block;
  }

  nav a {
    text-decoration: none;
    border: none;
    color: rgb(10, 162, 124);
    background: none;
    border-radius: none;
    box-shadow: none;
    margin-top: 0%;
    margin-left: -80%;
    border-image: none;
    transition: all 0.5s;
  }
  a {
    margin-top: -1%;
  }
  nav a:hover {
    box-shadow: none;
  }

  nav span {
    display: none;
  }

 
  #Ari{
    display: block;
    border-radius: 0%;
    position: absolute;
    color: white;
    font-weight: bold;
    padding: 10%;
    margin-left: -37%;
    width: 100vw;
    height: 100vh;
    text-align: center;

    background: linear-gradient(
      220deg,
      rgba(32, 21, 64, 0.3),
      rgba(25, 54, 61, 0.817)
    );
  }
}
