

  
  @media(orientation: portrait) and (max-width: 1020px) {
    .card {
        position: relative;
        margin-left: 15vw;
        margin-top: 25%;
        width: 65vw;
        height: 50vh;
        background-color: #000;
        display: flex;
        flex-direction: column;
        justify-content: start;
        padding: 12px;
        gap: 12px;
        border-radius: 8px;
        box-shadow: 10px 10px 40px rgb(110, 109, 109);
        color: white;
        height: auto;
      }
      
      .heading {
        font-size: 20px;
        text-transform: capitalize;
        font-weight: 700;
      }
      
      .card p:not(.heading) {
        font-size: 14px;
      }
      
      .card p:last-child {
        color: #e81cff;
        font-weight: 600;
        
          
        

      }
      .card img{
        border-radius: 25px;
        height: 25vh;
        object-fit: cover;
      }
  }
  